<template>
  <div class="phone-show">
    <div class="battery"></div>
    <div class="hiddenScrBox">
      <header class="header">
        <i class="el-icon-arrow-left back"
           @click="goBack"></i>
        <!-- <div class="back" @click="goBack">返回</div> -->
        <h2 class="titleMain">{{ form.title }}</h2>
        <div class="share"></div>
      </header>
      <div class="city_list">
        <img :src="form.recruitUrl"
             alt />
        <!-- <div class="btn">申请成为分销员</div> -->
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findChannelRecruitInfoById } from '@/api/distributionManagement/distributor'
export default {
  data() {
    return {
      id: '',
      form: {
        title: '',
        recruitUrl: '',
      }
    }
  },
  created() {
  },
  mounted() {
    this.getCardDetails()
  },
  methods: {
    getCardDetails() {
      findChannelRecruitInfoById({
        shopId: this.shop.shopId,
        id: this.$route.query.id
      }).then(res => {
        if (res.code == 200) {
          console.log(res)
          this.form = res.data
          this.loading = false
        }
      })
    },
    goBack() {
      this.$store.dispatch('delView', this.$route)
      this.$router.push({
        path: '/distributionManagement/distributor',
        query: {
          activeName: 'three'
        }
      })
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },

}
</script>
<style lang="scss" scoped>
.app-main {
  background: #fff;
}
.phone-show {
  width: 357px;
  font-size: 12px;
  margin: 53px auto 40px;
  height: 713px;
  padding: 46px 24px 22px 23px;
  background: url(../../../../assets/images/distribution/prewPhone.png)
    no-repeat;
  background-color: #fff;
  border-radius: 50px;
  position: relative;
  .city_list {
    width: 100%;
    height: 601px;
    overflow: auto;
    background-color: #f3f3f3;
    position: relative;
    img {
      width: 100%;
      // position: relative;
    }
    .btn {
      // position: absolute;
      // bottom: -117px;
      // left: 50%;
      width: 100%;
      height: 44px;
      background-image: linear-gradient(#ffe72d, #ffe832);
      font-size: 16px;
      color: #fb381e;
      text-align: center;
      line-height: 44px;
      border-radius: 22px;
      font-weight: bold;
    }
  }
}
.battery {
  width: 310px;
  height: 69px;
  position: absolute;
  left: 26px;
  top: 19px;
  background: url(../../../../assets/images/distribution/prewPhone.pnghead.png)
    no-repeat;
  background-size: 100% 100%;
}
.hiddenScrBox {
  width: 100%;
  height: 645px;
  overflow: hidden;
  border-radius: 0 0 28px 28px;
  position: relative;
  .header {
    box-sizing: border-box;
    width: 100%;
    height: 44px;
    padding: 10px 0;
    background-color: #fff;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    .back {
      margin-left: 20px;
      overflow: hidden;
      height: 24px;
      line-height: 24px;
      color: #000;
      font-size: 18px;
      // padding: 0 25px;
      text-align: center;
      cursor: pointer;
    }
    .titleMain {
      margin-left: -40px;
      margin-top: 0px;
      overflow: hidden;
      height: 24px;
      line-height: 24px;
      color: #000;
      padding: 0 25px;
      text-align: center;
      cursor: pointer;
    }
  }
}
.apply {
}

.fixedB {
  // width: 100%;
  // box-sizing: border-box;
  position: fixed;
  // left: 49%;
  bottom: 40px;
  z-index: 100;
  .btn-add {
    width: 200px;
  }
}

//隐藏滚动条
//chrome
.city_list::-webkit-scrollbar {
  width: 0 !important;
  height: 0 !important;
}
// IE10+
.city_list {
  -ms-overflow-style: none;
}
//firefox
.city_list {
  overflow: -moz-scrollbars-none;
}
</style>
<style lang="scss">
.rich_text {
  font-size: 14px;
  color: #333333;
  line-height: 22px;
  margin-top: 12px;
  overflow: hidden;
  p {
    width: 100%;
    img {
      width: 278px;
    }
  }
}
</style>
